@import url("https://fonts.googleapis.com/css2?family=Lemon&display=swap");
.btn-ic {
  min-width: 40px;
  width: 3%;
  max-width: 50px;
  cursor: pointer;
  margin-left: 7px;
}

.line-icons {
  display: flex;
  padding-top: 5px;
  padding-left: 10px;
}
.mid-left {
  margin-left: 2.5%;
  display: flex;
}
.playcontainer {
  width: 100%;
  height: 100%;
}
.timesptamp {
  font-size: 30px;
  margin-top: 4px;
  margin-left: 20px;
  color: white;
  /* padding-bottom: 13px; */
}
.content {
  position: initial;
  display: block;
}
.timeline-container {
  margin-top: 11px;
  display: flex;
  justify-content: center;
}
.timeline {
  width: 97%;
  height: 10px;
}
.img {
  /* vertical-align: bottom; */
}
.allcontainer {
  margin: auto;
  /* display: flex; */
  /* align-items: center; */
  /* justify-content: center; */
}

.right-icons {
  position: absolute;
  right: 0px;
  display: flex;
  justify-content: end;
  padding-right: 10px;
}
.settings {
  position: absolute;
  width: 160px;
  bottom: 72px;
  border-radius: 10px;
  right: 45px;
  background-color: #181818;
  opacity: 0.8;
  cursor: pointer;
}

.sett-obj {
  border-top: 10px;
  /* background-color: rgb(26, 23, 23); */
  height: 27px;
  color: white;
  display: flex;
  justify-content: center;
  padding-top: 5px;
  /* border-bottom: 1px solid white; */
}

.videos {
  background-color: black;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  position: relative;
}

.qual-label {
  left: 19px;
  top: -5px;
  position: absolute;
  color: white;
  font-size: 10px;
  border-radius: 2px;
  margin-left: 10px;
  margin-top: 5px;
  background-color: red;
}
.lang-label {
