.rd-ct {
  /* display: flex; */
  opacity: 0.1;
  position: absolute;
  height: 99%;
  width: 99%;
  background-repeat: no-repeat;
  background-size: cover;
}
.rd-img {
  border-radius: 10px;
  margin-left: 100px;
  height: 700px;
}
.rd-ctn {
  display: flex;
  margin-top: 180px;
  position: absolute;
}
.rd-data {
  z-index: 1;
  padding-top: 20px;
  display: block;
  margin-left: 5%;
}
.rd-name {
  text-decoration: underline;
  font-size: 40px;
  font-weight: bold;
  color: #fff;
}
.rd-desc {
  font-size: 20px;
  color: #fff;
  margin-top: 20px;
  width: auto;
  margin-right: 40px;
}
.rd-genre {
  margin-top: 20px;
  display: flex;
  color: white;
  font-size: 20px;
}
.rd-genre-item {
  text-decoration: underline;
}
.rd-year {
  margin-top: 20px;
  color: white;
  font-size: 20px;
  margin-right: 20px;
}
.rd-runtime {
  margin-top: 20px;
  color: white;
  font-size: 20px;
  margin-right: 20px;
}
.rd-dl {
  /* margin-top: 20px; */
  margin-left: 10px;
  color: white;
  background-color: transparent;
  border: 0px;
  /* font-size: 20px;
    margin-right: 20px; */
}
.rd-sl {
  /* margin-top: 20px; */
  margin-left: 10px;
  color: white;
  background-color: transparent;
  margin-top: 30px;
  border: 0px;
  /* font-size: 20px;
    margin-right: 20px; */
}
.rd-vote {
  margin-top: 20px;
  color: white;
  font-size: 20px;
  margin-right: 20px;
}
.rd-str {
  margin-top: 20px;
  color: white;
  font-size: 20px;
  margin-right: 20px;
  background-color: transparent;
  border: none;
}
.ep-ctn {
  width: calc(100% - 100px);
  height: 500px;
  overflow: auto;
}
.ep-ctn::-webkit-scrollbar {
  display: none;
}
.ep-name {
  display: flex;
  color: white;
}
.ep-item {
  display: flex;
  margin-top: 20px;
}
.ep-desc {
  color: white;
}
.ep-info {
  margin-left: 10px;
  display: block;
}
.ep-img {
  height: 110px;
  width: 200px;
  border-radius: 10px;
  background-color: red;
}
