:root{
    --po-radius : 10px;
    --po-height : 390px;
    --po-transition : 0.3s;
    --po-scale : 1.1;
    --po-img-size-w : 300px;
    --po-img-size-h : 420px;
}

.po-img {
    width: var(--po-img-size-w);
    height: var(--po-img-size-h);
    border-radius: var(--po-radius);
    border: 1px solid transparent;
}
.po-ct {
    position: relative;
    padding-top: calc((var(--po-height) * var(--po-scale) - var(--po-height)) / 2);
    /* height: var(--po-height);
     */

    width: 300px;
    transition: transform var(--po-transition) ease-in-out , margin-bottom var(--po-transition) ease-in-out;
    margin-bottom: 40px;
}
.po-name {
    font-size: 20px;
    font-weight: bold;
    color: #000;
    position: absolute;
}
.po-ct:hover{
    z-index: 3;
    transition: transform var(--po-transition) ease-in-out , margin-bottom var(--po-transition) ease-in-out ;
    /* transition-delay: 0.2s; */
    transform: scale(1.1);
    margin-bottom: 160px;
}
.po-img:hover{
   transition: border-radius var(--po-transition) ease-in-out, ;
   border-radius: 0;
   border: 1px solid gold;

}
.po-info {
    display:none;
    position: absolute;
    z-index: 2;
}
.po-info-name {
    color: white;
    text-decoration: underline;
    font-size: 20px;

}
.po-info-desc {
    color: white;
    overflow: auto;
    height: 120px;
}
.po-info-desc::-webkit-scrollbar {
    display: none;
}
