:root {
  --hd-height: 70px;
}

.hd-ctn {
  color: white;
  position: fixed;
  width: 100%;
  height: var(--hd-height);
  background-color: black;
  z-index: 10;
}
.hd-left {
  margin-left: 10px;
  margin-top: calc((var(--hd-height) - 30px) / 2);
  display: flex;
  font-size: 30px;
}
.hd-link {
  margin-left: 10px;
}
.hd-search {
  margin-left: 10px;
  font-size: 30px;
  margin-left: 10px;
}
